<template>
    <div>
        <basic-header :items="['店铺管理','商品管理']"></basic-header>
        <el-card>
            <el-tabs v-model="activeName" @tab-click="handleTabClick">
                <el-tab-pane label="商品查看" name="list">
                    <el-form :inline="true">
                        <el-form-item label="名称">
                            <el-input v-model="searchForm.name" :clearable="true"></el-input>
                        </el-form-item>
                        <el-form-item label="库存状态">
                            <el-select v-model="searchForm.store" :clearable="true">
                                <el-option v-for="item in options" :key="item.value" :value="item.value"
                                           :label="item.label"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="更新时间">
                            <el-date-picker
                                    v-model="searchForm.date"
                                    type="daterange"
                                    range-separator="至"
                                    start-placeholder="开始日期"
                                    end-placeholder="截止日期">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item>
                            <el-button circle type="primary" icon="el-icon-search" @click="search"></el-button>
                        </el-form-item>
                    </el-form>
                    <el-table :data="tableData">
                        <el-table-column label="id" prop="id"></el-table-column>
                        <el-table-column label="名称" prop="name"></el-table-column>
                        <el-table-column label="分类" prop="catname"></el-table-column>
                        <el-table-column label="描述" prop="description"></el-table-column>
                        <el-table-column label="缩略图" prop="thumb" align="center">
                            <template #default="o">
                                <img :src="o.row.thumb" alt="" width="100" height="100">
                            </template>
                        </el-table-column>
                        <el-table-column label="价格" prop="price" align="center"></el-table-column>
                        <el-table-column label="库存" prop="store"></el-table-column>
                        <el-table-column label="创建时间" prop="create_time">
                            <template #default="o">
                                {{formatDate(o.row.create_time)}}
                            </template>
                        </el-table-column>
                        <el-table-column label="更新时间" prop="update_time">
                            <template #default="o">
                                {{formatDate(o.row.update_time)}}
                            </template>
                        </el-table-column>
                        <el-table-column label="排序" prop="sort" align="center"></el-table-column>
                        <el-table-column label="上架状态" prop="state">
                            <template #default="o">
                                <div v-if="o.row.state===1">已上架</div>
                                <div v-else>已下架</div>
                            </template>
                        </el-table-column>
                        <el-table-column label="操作" width="100">
                            <template #default="o">
                                <el-button type="text" @click="del(o.row.id)">删除</el-button>
                                <el-button type="text" @click="edit(o.row.id)">编辑</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-pagination
                            background
                            style="margin-top:30px;"
                            layout="prev,pager,next"
                            :total="total"
                            :page-size.sync="pageSize"
                            :current-page.sync="page"
                            @current-change="change"
                    ></el-pagination>
                </el-tab-pane>
                <el-tab-pane label="商品添加" name="add">
                    <el-steps :active="active" finish-status="success">
                        <el-step title="基本信息">
                        </el-step>
                        <el-step title="详细信息">
                        </el-step>
                        <el-step title="配送信息">
                        </el-step>
                    </el-steps>
                    <el-form label-width="80px" :model="submitForm" :rules="submitRules" ref="submitForm">
                        <el-row>
                            <el-col v-show="active===0" :span="12">
                                <el-form-item label="商品分类" prop="tid">
                                    <el-select v-model="submitForm.tid">
                                        <el-option v-for="item in typeOption" :key="item.id" :value="item.id"
                                                   :label="item.name"></el-option>
                                    </el-select>
                                    <router-link :to="{name:'type'}" class="link">添加分类</router-link>
                                </el-form-item>
                                <el-form-item label="商品名称" prop="name">
                                    <el-input v-model="submitForm.name"></el-input>
                                </el-form-item>
                                <el-form-item label="商品介绍" prop="description">
                                    <el-input v-model="submitForm.description"></el-input>
                                </el-form-item>
                                <el-form-item label="商品价格" prop="price">
                                    <el-input type="number" v-model="submitForm.price"></el-input>
                                </el-form-item>
                                <el-form-item label="商品重量" prop="weight">
                                    <el-input type="number" v-model="submitForm.weight" placeholder="单位为千克"></el-input>
                                </el-form-item>
                                <el-form-item label="商品库存" prop="store">
                                    <el-input type="number" v-model="submitForm.store"></el-input>
                                </el-form-item>
                                <el-form-item label="商品排序" prop="sort">
                                    <el-input type="number" v-model="submitForm.sort"></el-input>
                                </el-form-item>
                                <el-form-item label="缩略图" prop="thumb">
                                    <el-upload
                                            class="avatar-uploader"
                                            :action="uploadUrl"
                                            :show-file-list="false"
                                            :on-success="handleAvatarSuccess"
                                            :before-upload="beforeAvatarUpload">
                                        <img v-if="submitForm.thumb" :src="submitForm.thumb" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                        <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                                    </el-upload>
                                </el-form-item>
                                <el-form-item label="banner" prop="banner">
                                    <el-upload
                                            class="upload-demo"
                                            :action="uploadUrl"
                                            list-type="picture"
                                            :before-upload="beforeAvatarUpload"
                                            :on-success="handleBannerSuccess"
                                            :on-remove="handleBannerRemove"
                                            ref="banner"
                                    >
                                        <el-button size="small" type="primary">点击上传</el-button>
                                        <div slot="tip" class="el-upload__tip">只能上传jpg文件，且不超过500kb</div>
                                    </el-upload>
                                </el-form-item>
                            </el-col>
                            <el-col v-show="active===1">
                                <el-form-item prop="content">
                                    <div ref="editor"></div>
                                </el-form-item>
                            </el-col>
                            <el-col v-show="active===2">
                                <el-form-item label="配送模板" prop="delivery_id">
                                    <el-select v-model="submitForm.delivery_id">
                                        <el-option v-for="item in deliveryOption" :key="item.id" :value="item.id"
                                                   :label="item.name"></el-option>
                                    </el-select>
                                    <router-link :to="{name:'type'}" class="link">添加模板</router-link>
                                </el-form-item>
                                <el-form-item label="是否上架">
                                    <el-switch
                                            v-model="submitForm.state"
                                            active-color="#13ce66"
                                            inactive-color="#ff4949">
                                    </el-switch>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="handleSubmit">提交</el-button>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                    <el-button-group style="margin-top:30px;">
                        <el-button type="primary" icon="el-icon-arrow-left" :disabled="active===0" @click="active--">
                            上一步
                        </el-button>
                        <el-button type="primary" :disabled="active===2" @click="active++">下一步<i
                                class="el-icon-arrow-right el-icon--right"></i></el-button>
                    </el-button-group>
                </el-tab-pane>
            </el-tabs>
        </el-card>
        <el-dialog
                title="编辑商品信息"
                :visible.sync="dialogVisible"
                width="80%"
        >
            <el-form :model="editForm" :rules="submitRules" label-width="80px" ref="editForm">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="商品分类" prop="tid">
                            <el-select v-model="editForm.tid">
                                <el-option v-for="item in typeOption" :key="item.id" :value="item.id"
                                           :label="item.name"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="商品名称" prop="name">
                            <el-input v-model="editForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="商品描述" prop="description">
                            <el-input v-model="editForm.description"></el-input>
                        </el-form-item>
                        <el-form-item label="商品价格" prop="price">
                            <el-input v-model="editForm.price" type="number"></el-input>
                        </el-form-item>
                        <el-form-item label="商品库存" prop="store">
                            <el-input v-model="editForm.store" type="number"></el-input>
                        </el-form-item>
                        <el-form-item label="商品排序" prop="sort">
                            <el-input v-model="editForm.sort" type="number"></el-input>
                        </el-form-item>
                        <el-form-item label="商品重量" prop="weight">
                            <el-input v-model="editForm.weight" type="number"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="缩略图" prop="thumb">
                            <el-upload
                                    class="avatar-uploader"
                                    :action="uploadUrl"
                                    :show-file-list="false"
                                    :on-success="handleAvatarSuccess1"
                                    :before-upload="beforeAvatarUpload">
                                <img :src="editForm.thumb" class="avatar">
                            </el-upload>
                        </el-form-item>
                        <el-form-item label="banner" prop="banner">
                            <el-upload
                                    class="upload-demo"
                                    :action="uploadUrl"
                                    :on-remove="handleBannerRemove1"
                                    :on-success="handleBannerSuccess1"
                                    :file-list="fileList"
                                    list-type="picture">
                                <el-button size="small" type="primary">点击上传</el-button>
                                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                            </el-upload>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item prop="content">
                          <div ref="editor2"></div>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="配送模板" prop="delivery_id">
                            <el-select v-model="editForm.delivery_id">
                               <el-option v-for="item in deliveryOption" :key="item.id" :value="item.id" :label="item.name"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="是否上架">
                            <el-switch
                                    v-model="editForm.state"
                                    active-color="#13ce66"
                                    inactive-color="#ff4949"
                                    :active-value="1"
                                    :inactive-value="0"
                            >
                            </el-switch>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="handleUpdate">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>
<script>
    import Header from "@/components/Header.vue"
    import wangeditor from "wangeditor"
    import formatDate from "@/utils/formatDate.js"
    export default {
        data(){
            return {
                tableData: [],
                activeName: "list",
                page: 1,
                pageSize: 5,
                total: 0,
                api: "/api/goods/goods",
                options: [
                    {label: "库存为空", value: 0},
                    {label: "库存正常", value: 1},
                ],
                searchForm: {
                    name: "",
                    store: "",
                    date: ""
                },
                active: 0,
                typeOption: [],
                submitForm: {
                    tid: "",
                    name: "",
                    description: "",
                    price: "",
                    sort: "",
                    thumb: "",
                    banner: "",
                    store: "",
                    content: "",
                    delivery_id: "",
                    weight: ""
                },
                submitRules: {
                    tid: {required: true, message: "请选择商品分类", trigger: "change"},
                    name: {required: true, message: "请设置商品名称", trigger: "blur"},
                    weight: {required: true, message: "请输入商品重量", trigger: "blur"},
                    description: {required: true, message: "请设置商品描述", trigger: "blur"},
                    price: {required: true, message: "请设置商品价格", trigger: "blur"},
                    sort: {required: true, message: "请设置商品排序", trigger: "blur"},
                    thumb: {required: true, message: "请设置商品缩略图"},
                    banner: {required: true, message: "请设置商品banner图"},
                    store: {required: true, message: "请设置商品库存量", trigger: "blur"},
                    content: {required: true, message: "请输入详细描述"},
                    delivery_id: {required: true, message: "请选择配送模板", trigger: "change"}
                },
                uploadUrl: "/api/upload/upload",
                editor: "",
                deliveryOption: [],
                editForm: {},
                dialogVisible: false,
                fileList: []
            }
        },
        methods: {
            fetchData(){
                let params={
                    page: this.page,
                    pageSize: this.pageSize
                };
                if (this.searchForm.name) {
                    params.name=this.searchForm.name;
                }
                if (this.searchForm.store !== "") {
                    params.store=this.searchForm.store;
                }
                if (this.searchForm.date) {
                    params.start_time=Math.floor(this.searchForm.date[0].getTime() / 1000);
                    params.end_time=Math.floor(this.searchForm.date[1].getTime() / 1000);
                }
                this.axios.get(this.api, {
                    params
                }).then(res => {
                    if (res.data.code === 200) {
                        this.tableData=res.data.data;
                        this.total=res.data.total;
                    }
                })
            },
            change(){
                this.fetchData();
            },
            search(){
                this.fetchData();
            },
            getAllType(){
                this.axios.get("/api/type/type").then(res => {
                    if (res.data.code === 200) {
                        this.typeOption=res.data.data;
                    }
                })
            },
            getAllDelivery(){
                this.axios.get("/api/delivery/delivery").then(res => {
                    if (res.data.code === 200) {
                        this.deliveryOption=res.data.data;
                    }
                })
            },
            handleTabClick(){
                if (this.activeName === "add") {
                    if (this.typeOption.length === 0) {
                        this.getAllType();
                        this.getAllDelivery();
                    }
                }
            },
            handleAvatarSuccess(res) {
                this.submitForm.thumb=res;
                this.$refs.submitForm.validateField("thumb");
            },
            handleAvatarSuccess1(res) {
                this.editForm.thumb=res;
                this.$refs.editForm.validateField("thumb");
            },
            beforeAvatarUpload(file) {
                const isJPG=file.type === 'image/jpeg';
                const isLt500k=file.size / 1024 < 500;
                if (!isJPG) {
                    this.$message.error('上传商品图片只能是 JPG 格式!');
                }
                if (!isLt500k) {
                    this.$message.error('上传商品图片大小不能超过500kb!');
                }
                return isJPG && isLt500k;
            },
            handleBannerSuccess(res, file, fileList){
                this.submitForm.banner=fileList.map(v => v.response).join(";");
                this.$refs.submitForm.validateField("banner");
            },
            handleBannerSuccess1(res, file, fileList){
                this.editForm.banner=fileList.map(v => v.response).join(";");
                this.$refs.editForm.validateField("banner");
            },
            handleBannerRemove(file, fileList){
                this.submitForm.banner=fileList.map(v => v.response).join(";");
                this.$refs.submitForm.validateField("banner");
            },
            handleBannerRemove1(file, fileList){
                this.editForm.banner=fileList.map(v => v.response).join(";");
                this.$refs.editForm.validateField("banner");
            },
            handleSubmit(){
                if (this.editor.txt.html() !== "<p><br></p>") {
                    this.submitForm.content=this.editor.txt.html();
                }
                this.$refs.submitForm.validate((valid) => {
                    if (valid) {
                        this.axios.post(this.api, this.submitForm).then(res => {
                            if (res.data.code === 200) {
                                this.$message.success("添加成功");
                                this.$refs.submitForm.resetFields();
                                this.active=0;
                                this.activeName="list";
                                this.editor.txt.clear();
                                this.fetchData();
                                this.$refs.banner.clearFiles();
                            }
                        })
                    } else {
                        this.$message.error("验证失败，请检查字段内容");
                    }
                })
            },
            edit(id){
                this.axios.get(this.api, {params: {id}}).then(res => {
                    if (res.data.code === 200) {
                        this.editForm=res.data.data;
                        this.fileList=this.editForm.banner.split(";").map((v, i) => ({name: i + 1 + ".jpg", url: v,response:v}));
                        if (this.typeOption.length === 0) {
                            this.getAllType();
                            this.getAllDelivery();
                        }
                        this.dialogVisible=true;
                        this.$nextTick(()=>{
                            this.editor2=new wangeditor(this.$refs.editor2);
                            this.editor2.customConfig.uploadImgServer='/api/upload/editorUpload';
                            this.editor2.customConfig.uploadFileName='file';
                            this.editor2.create();
                            this.editor2.txt.html(this.editForm.content);
                        });

                    }
                })
            },
            handleUpdate(){
                if (this.editor2.txt.html() !== "<p><br></p>") {
                    this.editForm.content=this.editor2.txt.html();
                }
                this.$refs.editForm.validate(valid=>{
                    if(valid){
                        delete this.editForm.create_time;
                        this.axios.put(this.api,this.editForm).then(res=>{
                            if(res.data.code===200){
                                this.dialogVisible=false;
                                this.$message.success('提交成功');
                                this.fetchData();
                            }
                        })
                    }else{
                        this.$message.error('验证失败');
                    }
                })
            },
            del(id){
                this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete(this.api,{
                        params:{
                            id
                        }
                    }).then(res=>{
                        if(res.data.code===200){
                            this.$message.success("删除成功");
                            this.fetchData();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            formatDate
        },
        components: {
            "basic-header": Header
        },
        watch: {
            active(value){
                if (value === 1 && this.editor === "") {
                    //当前值修改之后 dom也相应的完成一次更新会执行此回调函数
                    this.$nextTick(() => {
                        this.editor=new wangeditor(this.$refs.editor);
                        this.editor.customConfig.uploadImgServer='/api/upload/editorUpload';
                        this.editor.customConfig.uploadFileName='file';
                        this.editor.create();
                    });
                }
            }
        },
        created(){
            this.fetchData();
        },
        mounted(){

        }
    }
</script>
<style scoped>
    .avatar-uploader .el-upload {
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }

    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }

    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
        border: 1px dashed #d9d9d9;
    }

    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }

    .link {
        margin-left: 30px;
        text-decoration: none;
        color: #409EFF;
    }
</style>